import React, { useState, useEffect } from 'react';
import { QRCodeCanvas } from 'qrcode.react';
import { toPng } from 'html-to-image';
import { saveAs } from 'file-saver';
import { Input, Button, List, Card, Typography, message } from 'antd';

const { Title } = Typography;

function App() {
  const [name, setName] = useState('');
  const [participants, setParticipants] = useState([]);

  // بارگذاری داده‌ها از localStorage
  useEffect(() => {
    const saved = localStorage.getItem('participants');
    if (saved) {
      setParticipants(JSON.parse(saved));
    }
  }, []);

  // ذخیره داده‌ها در localStorage
  useEffect(() => {
    localStorage.setItem('participants', JSON.stringify(participants));
  }, [participants]);

  const handleGenerate = () => {
    if (!name.trim()) {
      message.warning('لطفاً نام خریدار را وارد کنید');
      return;
    }
    const entry = { name, id: Date.now() };
    setParticipants([...participants, entry]);
    setName('');
    message.success('QR Code با موفقیت تولید شد');
  };

  const handleSaveQRCode = async (id) => {
    const node = document.getElementById(`qr-wrapper-${id}`);
    try {
      const dataUrl = await toPng(node);
      saveAs(dataUrl, `QRCode-${id}.png`);
      message.success('تصویر ذخیره شد');
    } catch (error) {
      console.error('خطا در ذخیره QR:', error);
      message.error('خطا در ذخیره تصویر');
    }
  };

  return (
    <div
      style={{
        padding: 30,
        maxWidth: 600,
        margin: '0 auto',
        backgroundColor: '#0b1a33',
        color: 'white',
        minHeight: '100vh',
        direction: 'rtl',
      }}
    >
      <Title level={3} style={{ color: '#aad3ff' }}>
        🎁 قرعه‌کشی فروشگاه شهاب
      </Title>

      <Input.Group compact style={{ marginBottom: 20 }}>
        <Input
          style={{
            width: '70%',
            backgroundColor: '#14284b',
            color: '#fff',
            borderColor: '#334d73',
          }}
          value={name}
          onChange={(e) => setName(e.target.value)}
          placeholder="نام خریدار را وارد کنید"
          placeholderTextColor="#ccc"
        />
        <Button
          type="primary"
          onClick={handleGenerate}
          style={{ backgroundColor: '#1f3b67', borderColor: '#1f3b67' }}
        >
          تولید QR
        </Button>
      </Input.Group>

      <List
        dataSource={participants}
        renderItem={(p) => {
          const qrValue =
            `خریدار: ${p.name}\n` +
            `فروشگاه: فروشگاه نوشت‌افزار شهاب\n` +
            `آدرس: روستای کولفرح\n` +
            `نویسنده: حسین فرضعلی‌وند\n` +
            `کد: ${p.id}`;

          return (
            <List.Item key={p.id}>
              <Card
                title={`نام: ${p.name}`}
                style={{
                  width: '100%',
                  backgroundColor: '#14284b',
                  color: 'white',
                  borderColor: '#334d73',
                }}
                headStyle={{ color: '#aad3ff' }}
                bodyStyle={{ color: 'white' }}
                actions={[
                  <Button
                    key="save"
                    onClick={() => handleSaveQRCode(p.id)}
                    style={{ backgroundColor: '#1f3b67', color: '#fff' }}
                  >
                    ذخیره QR
                  </Button>,
                ]}
              >
                <div
                  id={`qr-wrapper-${p.id}`}
                  style={{
                    backgroundColor: '#0b1a33',
                    padding: 20,
                    borderRadius: 10,
                    display: 'inline-block',
                    textAlign: 'center',
                    color: '#ffffff',
                    fontSize: 14,
                    fontFamily: 'Tahoma, sans-serif',
                  }}
                >
                  <QRCodeCanvas value={qrValue} size={150} />
                  <div style={{ marginTop: 10, whiteSpace: 'pre-line' }}>
                    <div>🏪 فروشگاه نوشت‌افزار شهاب</div>
                    <div>📍 روستای کولفرح</div>
                    <div>👨‍💻 حسین فرضعلی‌وند</div>
                  </div>
                </div>
              </Card>
            </List.Item>
          );
        }}
        locale={{ emptyText: 'هنوز خریداری ثبت نشده' }}
      />
    </div>
  );
}

export default App;
